import React from "react";
import CelebrityList from "./CelebrityList";
export default class CelebrityCreate extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          name:'',
          image:'',
          des:''
        }
        
        this.handleNameChange = this.handleNameChange.bind(this);
        this.handleImageChange = this.handleImageChange.bind(this);
        this.handleDesChange = this.handleDesChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this)
    };
     handleNameChange(event) {
        this.setState({name: event.target.value});
      }
      handleImageChange(event) {
        this.setState({image: event.target.value});
      }
      handleDesChange(event) {
        this.setState({des: event.target.value});
      }
      handleSubmit=(event)=> {
    
        fetch('https://bai03.herokuapp.com/api/celebrities', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                name: this.refs.name.value,
                image_url: this.refs.image.value,
                description: this.refs.des.value
            })
            })
            .then(
               (result)=>{
                    this.props.afterCreate();
                    this.props.close();
               },
               (error)=>{
                    console.log(error);
               }
            )
      }
    render(){
        return (
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title" id="exampleModalLabel">Add new celebrity</h5>
                    </div>
                    <div className="modal-body">
                        <p>Name: <input onChange={this.handleNameChange}  ref="name" className="form-control" id="Ten" type="text" name="name"></input></p>
                        <p>Image url: <input onChange={this.handleImageChange}  ref="image" className="form-control" id="Hinh" type="text" name="image_url"></input></p>
                        <p>Description: <textarea onChange={this.handleDesChange} ref="des" className="form-control" id="GhiChu" name="description" rows="5"></textarea></p> 
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-primary" onClick={this.handleSubmit}>Add</button>
                        <button type="button"  className="btn btn-seccondary" onClick={this.props.close}>close</button>
                    </div>
                </div>
           );
    }
}
 


