import React, {Component} from 'react';
import logo from './logo.svg';
import './App.css';
import CelebrityList from "./components/CelebrityList";
import CelebrityCreate from "./components/CelebrityCreate";
import Modal from 'react-modal';
class App extends Component {
    constructor() {
        super();

        this.state = {
            modalIsOpen: false,
            items: [],
            error: null,
            isLoaded: false
        };
    }
    openModal = () => {
        this.setState({modalIsOpen: true});
    }
    closeModal = () => {
        this.setState({modalIsOpen: false});
    }
    componentDidMount() {
    
        fetch("https://bai03.herokuapp.com/api/celebrities")
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        items: result
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
            )
    }
    Reload =()=>
    {
        this.componentDidMount();
    }
    render() {
        return (
            <div className="App">
                <header className="App-header">
                    <img src={logo} className="App-logo" alt="logo"/>
                    <h1 className="App-title">Welcome to React</h1>
                </header>
                <button onClick={this.openModal} className="btn btn-primary">Add new celebrity</button>
                <Modal
                isOpen={this.state.modalIsOpen}
                onRequestClose={this.closeModal}
                className="modal-dialog"
                >
                     <CelebrityCreate afterCreate = {this.Reload} close={this.closeModal}/>
                </Modal>
                <div id="list">
                    <CelebrityList 
                        erorr={this.state.error}
                        isLoaded={this.state.isLoaded}
                        items={this.state.items}
                    />
                </div>
            </div>
        );
    }
}

export default App;



